button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.navBar {
  height: 100px;
  background: #faf0d7;
  line-height: 100px;
  padding-left: 40px;
  font-size: 33px;
  font-weight: 400;
}

.fa-leaf {
  color: green;
  margin-right: 15px;
}

.input-container {
  display: flex;
  align-items: center;
  gap: 5px;

  margin: 10px;
}

#add-habit {
  height: 40px;
}

.add-button {
  background-color: green;
  color: white;
  line-height: 30px;
  font-size: 30px;
  padding: 5px 10px;
  border: 0;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-count,
.habits-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  font-size: 2rem;
  color: darkred;
}
